<template>
    <div>
        <input type="text" v-model="username" placeholder="" />
        <input type="password" v-model="password" placeholder="" />
        <button @click="submit"></button>
        <div class="console" id="terminal"></div>
    </div>
</template>

<script>
import "xterm/css/xterm.css";
import { Terminal } from "xterm";
import { FitAddon } from "xterm-addon-fit";
export default {
    name: "Xterm",
    props: {},
    data() {
        return {
            term: null,
            socket: null,
            // rows: 40,
            // cols: 10,
            SetOut: false,
            isKey: false,
            password: "i3hw!i4iglp6n3aKdW2WW",
            username: "root"
        };
    },
    activated() {},
    methods: {
        //Xterm
        initTerm() {
            const term = new Terminal({
                rendererType: "canvas", //
                // rows: this.rows, //
                // cols: 10, // 
                // scrollback: 10,//
                convertEol: true, //
                fontSize: 14, //
                disableStdin: false, //
                cursorStyle: "block", //
                cursorBlink: true, //
                // scrollback: 30,
                tabStopWidth: 4,
                theme: {
                    foreground: "yellow", //
                    background: "#060101", //
                    cursor: "help" //
                }
            });
            const fitAddon = new FitAddon();
            term.loadAddon(fitAddon);
            term.open(document.getElementById("terminal"));
            term.focus();
            //
            this.$socket.on(this.$socket.id + "_ssh", function(data) {
                term.write(data);
            });
            term.write(
                "\rAcross the Great Wall we can reach every corner in the world.\n$ "
            );
            term.onData(key => {
                this.$socket.emit(this.$socket.id + "_ssh", key);
            });
        },
        //webShell
        initSocket() {
            console.log(1);
        },
        submit() {
            if (!this.$socket.id) {
                let timeOut = setTimeout(() => {
                    this.submit();
                    clearTimeout(timeOut);
                }, 500);
                return;
            }
            this.$socket.emit("createNewSSH", {
                id: this.$socket.id,
                ip: "106.13.184.92",
                username: this.username,
                password: this.password
            });
            this.initTerm();
        }
    }
};
</script>


<style lang="scss"  scoped>
#terminal {
    width: 100%;
    &::-webkit-scrollbar {
        display: none;
    }
}
</style>